@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@600&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* ///////////////////////////////////NAVBAR///////////////////////// */

.navbar-container{
  /* border: 2px solid black; */
  background-color: #e1e4e2;
  display: flex;
  justify-content:space-between;
  padding: 1em 2em;
}

.navbar-right-container{
  display: flex;
  overflow: hidden;
}
.navbar-right-container .icon{
  display: none;
}

.button-1{
  text-decoration: none;
  color: black;
  padding: 0.3125em 0.625em;
  margin: 0.625em;
  border: 0.125em solid #e1e4e2;
  background-color: #e1e4e2;
  font-size: 1em;
  border-radius: 1.562em;

}

.button-1:hover{
  border-radius: 1.562em;
  cursor: pointer;
  background-color: rgb(241, 255, 253);
  transition: 0.3s ease-in-out;
  color: black;
}



/* ///////////////////////////////////Home Section///////////////////////// */


.home-container{
  background: url("/portfolio/static/media/bg (1).e31167c4.png") no-repeat center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  height: 100%;
}
.home-container-2 {
  display: flex;
}
.home-container-2 img{
  height: 20em;
  width: 15em;
  border-radius: 50%;
  box-shadow: 0 1.875em 2.5em rgba(0,0,0,.1);
  margin-left: 15%;
  margin-right: 10%;
  margin-top: 0;
}
.hey{
  font-size: 1em;
}
.info-container h1{
  margin-top: 1em;
}
.home-container h2{
  font-size: 2em;
  margin: 1em 0;
}

.name{
  color: #e1e4e2;
  margin: 0.5em 2em;
  font-size: 3em;
  font-family: 'Acme', sans-serif;
}
.button-2{
  color: #e1e4e2;
  text-decoration: none;
  border: 0.125em solid #e1e4e2;
  border-radius: 1.25em;
  padding: 0.3em;
  transition: 0.3s ease-in-out;
}
.button-2:hover{
  background-color: #e1e4e2;
  color: black;
}


.typed-text{
  color: white;
}

.social-media-container{
  display: flex;
  justify-content: center;
  align-items: center;
  color: #e1e4e2;
  margin-top: 1.88em;
}

.social-media-icons img{
  border-radius: 2.5em;
  /* margin: 0.6rem; */
  box-sizing: content-box;
  padding: 0.5em;
}

.social-media-icons a:hover{
  -webkit-filter: invert(0.5) sepia(1) hue-rotate(200deg);
          filter: invert(0.5) sepia(1) hue-rotate(200deg);                                  
}


.social-media-container img{
  -webkit-filter: invert(1);
   filter: invert(1);
   height: 1.5em;
   width: 1.5em;
}
.social-media-container h4{
  font-family: 'Nunito', sans-serif;
  font-size: 1em;
}


/* ///////////////////////////////////About Section///////////////////////// */
.common-container{
  background: url(/portfolio/static/media/about-bg.05cb44d5.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  color: white;
}
.navlink2{
  color: #e1e4e2;
}
.tsparticles-canvas-el{
  position: absolute;
  width: 100vw;
  height: 100%;
  z-index: 1;
}

.common-inner-container{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80vw;
  margin: auto;
  background-color: black;
  z-index: 3;
}
.common-inner-container .hr-line{
  color: #e1e4e2;
  width: 50%;
  /* margin-bottom: 1.875em; */
}
.common-inner-container div{
  display: flex;
  flex-direction: column;
  padding: 1em 5em;
  min-width: 75vw;
  z-index: 3;
}
.common-inner-container div h1{
  margin-top: 0.2em;
}


/* ///////////////////////////////////Contact Section///////////////////////// */

.inner-container{
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  width: 80vw;
  margin: auto;
  padding: 1em;
  background-color: black;
  z-index: 3;
}
.contact-contents{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 4;
  width: 70%;
  padding: 1em;
}
.contact-contents form{
  width: 100%;
}
.contact-contents div{
  /* display: flex; */
  width: 100%;
  margin: 0.498em;
}
.contact-contents label{
  display: block;
  letter-spacing: 0.1em;
  /* margin: 0.2em; */
}
.contact-contents div input, .message{
  margin-left: 0.625em;
  padding: 0.2em 0.4em;
  border-radius: 1.25em;
  width: 100%;
  letter-spacing: 0.1em;
  border: none;
  background-color: #e1e4e2;
  font-size: 0.7em;
}

.button-contact{
  color: #e1e4e2;
  text-decoration: none;
  border: 2px solid #e1e4e2;
  border-radius: 1.25em;
  padding: 0.4em;
  font-size: 0.8em;
  transition: 0.1s ease-in-out;
  background-color: black;
  width: 60%;
  margin: auto;
  letter-spacing: 0.3em;
  font-weight: bold;
}
.button-contact:hover{
  background-color: #e1e4e2;
  color: black;
  cursor: pointer;
}
.msg-sent{
  text-align: center;
}





/* ///////////////////////////////Projects/////////////////////////// */

.project-container{
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: row;
  padding: 2em;
  color: #e1e4e2;
}
.card-container{
  box-sizing: border-box;
  border: 2px solid #e1e4e2;
  margin: 0.8em;
  padding: 0.8em;
  min-width: 10em;
  max-width: 30%;
  background-color: black;
  opacity: 0.8;
  border-radius: 1em;
  z-index: 2;
}
/* //////////////////////////////////Card/////////////////////////// */
.project-card{
  position: relative;
}


.popup-behind{
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.card-inner-text{
  margin: 1em 0;
  font-weight: 300;
  font-size: 1em;
  /* font-family: 'Nunito', sans-serif; */
}


.button-card{
  background-color: transparent;
  color: #e1e4e2;
  cursor: pointer;
  border: 1px solid #e1e4e2;
  padding: 0.1em;
}

.cara-img{
  /* min-height: 100px; */
  max-height: 25em;
}

.button-popup{
  background-color: white;
  color: black;
  border: white;
  border-radius: 20%;
  padding: 0.2em 0.5em;
  margin: 0.3em;
  font-size: 0.8em;
  transition: 0.3s ease-in-out;
}
.button-popup:hover {
  background-color: #becac2;
}

.carousel-control-next-icon, .carousel-control-prev-icon{
  -webkit-filter: invert(100%)!important ;
          filter: invert(100%)!important ;
}

.tt{
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.overview-head{
  z-index: 100;
  color: black;
  margin: 0.3em;
  align-self: center;
  font-size: 1.5em;
}

.modal-inner2{
  position: absolute;
  background-color: black;
  color: #e1e4e2;
  /* opacity: 0.95; */
  top: 1em;
  left: 1em;
  bottom: 1em;
  right: 1em;
  width: auto;
  height: auto;
  z-index: 3;
}
#carouselExampleIndicators{
  z-index: 5;
}
.fade, .modal-backdrop, .show{
  z-index: 4;
}

/* ///////////////////////////////Footer/////////////////////////// */

.footer{
  background-color: black;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #e1e4e2;
  margin: 0;
  padding: 0.438em;
  font-family: 'Nunito', sans-serif;
  font-size: 0.79em;
  height: 100%;
}



/* ///////////////////Responsiveness//////////////// */


@media (max-width: 1160px) {
  .navbar-container{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .hey-there, .name{
    display: none;
  }
  .hey{
    color: #e1e4e2;
  }
  .home-container-2{
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .resp-button-2{
    display: flex;
    justify-content: center;
    align-items: center;
  }
}


@media screen and (max-width: 500px){
  .navbar-right-container .navlink {display: none;}
  .navbar-right-container .navlink.icon {
    float: right;
    display: block;
  }

  /* .navbar-right-container.responsive {position: relative;} */
  .navbar-right-container.responsive .icon {
    position: absolute;
    right: 1em;
    top: 1em;
  }

  .navbar-right-container.responsive .navlink {
    float: none;
    display: block;
    text-align: center;
  }

  .navbar-right-container{
    flex-direction: column;
  }
  .button-1{
    padding: 0.1em;
  }
  .navbar-right-container .icon{
    position: absolute;
    right: 1em;
    top: 1em;
  }

  .disable{
    display: none;
  }
  .card-container{
    max-width: 100%;
  }
  .inner-container{
    padding: 1em 0.5em;
    margin: 0;
    width: 100%;
  } 

  .modal-content{
    height: 80%;
  }
}
